.promo {
  padding-top: 26px; //          
  text-align: center;
  background-color: #63A63C;

  background-image: url("../img/background/bg-promo-mobile.jpg"),
                    linear-gradient(#63a63b 0, #63a63b 302px, $white 302px);

  background-repeat: no-repeat;
  background-position: 50% 0;
  @media (min-resolution: 2dppx) {
    background-image: url("../../img/background/bg-promo-mobile@2x.jpg"),
                      linear-gradient(#63a63b 0, #63a63b 302px, #ffffff 302px);
    background-size: auto 302px, //    
                     auto auto; //   - (   background-size       background-image        background-image)
  }


  @media (min-width: $tablet-width) {
    padding-top: 33px;
  }

  @media (min-width: $desktop-width) {
    padding-top: 0;
    position: relative;
  }

  &::before {
//      
    @media (min-width: $desktop-width) {
      background-color: #63A63C;
      position: absolute;
      z-index: -2; //      ,    -1,         .promo__image   z-index        (promo::after)
      content: "";
      top: -300px; //       
      right: 0; //    
      bottom: 82px; //    
      left: 50%; //      
      background-repeat: no-repeat;
      background-position: left bottom;
      background-size: 1044px 696px;
      background-image: url("../../img/background/bg-promo-desktop.jpg"); //       ,                png    (     )        paint   png        jpg)         
      
      @media (min-resolution: 2dppx) {
        background-image: url("../../img/background/bg-promo-desktop@2x.jpg");
      }
    }

  }

  &::after {
    background-color: rgba(104, 183, 56, 0.85); //    after   before     
  }

  @media (max-width: $mobile-width-only) {
    color: $white; //       
  }

  @media (min-width: $tablet-width) {
    color: $black; //  :   ,
    background-image: none; //  ,
    background-color: transparent; //  
    text-align: left; //     
  }
}

.promo__container { // e    c container,                
  @media (min-width: $tablet-width) {
    position: relative; //   -
    padding: 0 90px 526px; // 90px        , 526px   ( )    
  }
  @media (min-width: $desktop-width) {
    min-height: 636px; //    
    padding: 85px 610px 95px 80px; // 80px
  }
}

.promo__heading {  // :    ....
  margin: 0;
  margin-bottom: 25px;
  font-size: 36px;
  line-height: 1;
  font-family: $font-heading;
  font-weight: 400;

  @media (min-width: $tablet-width) {
    margin-bottom: 39px;
    font-size: 60px;
  }

}

.promo__text {
  margin-top: 0;
  margin-bottom: 29px;

  font-family: $font-heading;
  font-size: 14px;
  line-height: 1;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    margin-bottom: 50px;
  }
}

.promo__image {
  width: 280px;
  @media (min-width: $tablet-width) {
    position: absolute;
    left: 0;
    bottom: 0;
    z-index: -1;
    width: 709px;
    height: 609px;
  }
  @media (min-width: $desktop-width) {
    top: 0;
    left: calc(50% - 162px); //           162px;
    width: 552px;
    height: 499px;
  }
}

.promo__button {
  width: 100%;
}
