.logo { //     c @import "blocks/logo.scss";  style.scss
  display: grid; //   ,    .
  grid-template-columns: 34px 1fr; //           .page-header__bar,     logo.
  align-items: center; //           .page-header__logo
  gap: 16px; //              .page-header__bar

  @media (min-width: $tablet-width) {
    grid-template-columns: 60px 1fr;
    gap: 12px;
  }

  @media (min-width: $desktop-width) {
    grid-template-columns: 70px 1fr;
    gap: 15px;
  }
  
  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }
}

.logo__image {
  width: 34px;
  height: 38px;
  @media (min-width: $tablet-width) {
    width: 60px;
    height: 52px;
  }
  @media (min-width: $desktop-width) {
    width: 70px;
    height: 59px;
  }
}

.logo__sign {
  display: block;
  width: 101px; 
  height: 18px;
  justify-self: center; //       
  
  @media (min-width: $tablet-width) {
    justify-self: start; //       
  }
}


.page-footer__logo { 
  grid-template-columns: auto; //    display: grid;      ( )
  margin-left: auto;
  margin-right: auto;
  width: 128px;
  height: 24px;

  @media (min-width: $tablet-width) {
    margin: 0;
    margin-top: 4px;
  }
}
